
import { defineComponent, reactive, onMounted } from "vue";
import BarChartStraight from "@/components/chart/BarChartStraight.vue";
import PieChart from "@/components/chart/PieChart.vue";
import axios from "axios";
import { IFinanceStatistics } from "@/models/interface/financeStatistics";

export default defineComponent({
  components: { BarChartStraight, PieChart },
  setup() {
    const data = reactive({
      pieStructure: [
        { label: "", value: 0 }, 
        { label: "", value: 0 },
        { label: "", value: 0 },
      ],
      pieDifficult: [
        { label: "/", value: 0 }, 
        { label: "", value: 0 },
        { label: "", value: 0 },
        { label: "", value: 0 },
        { label: "", value: 0 },
      ],
      barRevenue: { 
        xAxis: [ "", "", "" ],
        data: [ 0, 0, 0 ],
      },
      barSumPeople: { 
        xAxis: [ "", "", "" ],
        data: [ 0, 0, 0 ],
      },
      barProfits: { 
        xAxis: [ "", "", "" ],
        data: [ 0, 0, 0 ],
      },
      barDevelop: { 
        xAxis: [ "", "", "" ],
        data: [ 0, 0, 0] ,
      },
    });

    onMounted(() => {
      axios
        .get<IFinanceStatistics>("./data/finance_statistics.json")
        .then((res) => {
          data.pieStructure = [
            { label: "", value: res.data.structure.asset },
            { label: "", value: res.data.structure.equity },
            { label: "", value: res.data.structure.debt },
          ];
          data.pieDifficult = [
            { label: "/", value: res.data.difficult[1] },
            { label: "", value: res.data.difficult[2] },
            {
              label: "",
              value: res.data.difficult[3],
            },
            { label: "", value: res.data.difficult[4] },
            { label: "", value: res.data.difficult[5] },
          ];
          data.barRevenue = {
            xAxis: ["", "", ""],
            data: res.data.revenue,
          };
          data.barSumPeople = {
            xAxis: ["", "", ""],
            data: res.data.sumPeople,
          };
          data.barProfits = {
            xAxis: ["", "", ""],
            data: res.data.profits,
          };
          data.barDevelop = {
            xAxis: ["", "", ""],
            data: res.data.develop,
          };
        });
    });
    return { data };
  },
});
