
import { defineComponent, reactive, onMounted } from "vue";
import BarChartHorizontal from "@/components/chart/BarChartHorizontal.vue";
import PieChart from "@/components/chart/PieChart.vue";
import axios from "axios";
import { IIntelligentStatistics } from "@/models//interface/intelligentStatistics";

export default defineComponent({
  components: { BarChartHorizontal, PieChart },
  setup() {
    const data = reactive({
      pieValueChainTool: [
        { label: "", value: 0 }, 
        { label: "", value: 0 },
      ],
      barValueChainTool: [
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
      ],
      pieResearchTool: [
        { label: "", value: 0 }, 
        { label: "", value: 0 },
      ],
      barResearchTool: [
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
      ],
      pieOperationTool: [
        { label: "", value: 0 }, 
        { label: "", value: 0 }
      ],
      barOperationTool: [
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
        { name: "", data: 0, max: 0 },
      ]
    });

    onMounted(() => {
      axios
        .get<IIntelligentStatistics>("./data/intelligent_statistics.json")
        .then((res) => {
          data.pieValueChainTool = [
            {
              label: "",
              value: res.data.valueChainTool.isUseTool[0],
            },
            {
              label: "",
              value: res.data.valueChainTool.isUseTool[1],
            },
          ];
          data.barValueChainTool = [
            { name: "", data: res.data.valueChainTool.order, max: 100 },
            { name: "", data: res.data.valueChainTool.carry, max: 100 },
            { name: "", data: res.data.valueChainTool.customer, max: 100 },
            { name: "", data: res.data.valueChainTool.analyze, max: 100 },
          ];
          data.pieResearchTool = [
            {
              label: "",
              value: res.data.researchTool.isUseTool[0],
            },
            {
              label: "",
              value: res.data.researchTool.isUseTool[1],
            },
          ];
          data.barResearchTool = [
            { name: "", data: res.data.researchTool.design, max: 100 },
            { name: "", data: res.data.researchTool.monitor, max: 100 },
            { name: "", data: res.data.researchTool.auto, max: 100 },
            { name: "", data: res.data.researchTool.manage, max: 100 },
            { name: "", data: res.data.researchTool.analyze, max: 100 },
          ];
          data.pieOperationTool = [
            {
              label: "",
              value: res.data.operationTool.isUseTool[0],
            },
            {
              label: "",
              value: res.data.operationTool.isUseTool[1],
            },
          ];
          data.barOperationTool = [
            { name: "", data: res.data.operationTool.erp, max: 100 },
            { name: "", data: res.data.operationTool.hrs, max: 100 },
            { name: "", data: res.data.operationTool.fas, max: 100 },
            { name: "", data: res.data.operationTool.ics, max: 100 },
            { name: "", data: res.data.operationTool.analyze, max: 100 },
          ];
        });
    });
    return { data };
  },
});
